.banner{
    width: 100%;
    background: #032541;
    position: relative;
}
.banner .imgbox{
    margin: 0 auto;
    width: 1216px;
    height: 360px;
}
.banner .imgbox img{
    width: 1216px;
    height: 100%;
}

.f1_inner{
    width: 100%;
    margin: 0 auto;
    background: #fff;
}
.f1>fieldset{
    border: none;
}
.f1>fieldset>legend{
    position: absolute;
    left: -99999px;
}
.f1>fieldset>input{
    width: 1216px;
    height: 50px;
    background: #fff;
    border: none;
    font-size: 20px;
    outline: none;
    padding-left: 352px;
}
.f1>fieldset>input::placeholder{
    color: #727377;
    font-size: 16px;
    font-weight: 500; 
}

.f2>fieldset{
    border: none;
}
.f2>fieldset>legend{
    position: absolute;
    left: -99999px;
}
.f2>fieldset>input{
    width: 1216px;
    height: 50px;
    border-radius: 999px;
    background: #fff;
    border: none;
    font-size: 20px;
    outline: none;
    padding-left: 30px;
}
.f2>fieldset>input::placeholder{
    color: #636c7d;
    font-size: 16px;
    font-weight: 500;
}
.f2>fieldset>button{
    width: 80px;
    height: 50px;
    background-size: 30px;
    margin-left: -80px;
    background: #10C9C1;
    border-radius: 999px;
    color: #fff;
    font-size: 15px;
}

.searchbox{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    color: #fff;
}
.searchbox h1{
    font-size: 45px;
}
.searchbox h3{
    margin-top: -10px;
    margin-bottom: 52px;
    font-size: 30px;
    color: #fff;
}