.second{
    width: 100%;
    height: 380px;
    background: #00325C;
}
.second .second_inner{
    width: 1216px;
    height: 100%;
    padding: 30px 0;
    margin: auto;
    position: relative;
}
.second .second_inner h2{
    font-size: 22px;
    font-weight: 800;
    color: #fff;
}
.second .second_inner .genre{
    display: flex;
    margin-bottom: 20px;
}
.second .second_inner .genre ul{
    display: flex;
    align-items: center;
    margin-left: 20px;
    border-radius: 999px;
    gap: 4px;
    border: 1px solid #fff;
    color: #fff;
}
.second .second_inner .genre ul li{
    padding: 2px 12px;
    cursor: pointer;
    font-weight: 700;
}
.second .second_inner .genre ul li.first{
    border: 1px solid #000;
    border-radius: 999px;
    background: #38DBB0;
    color: #00473C;
}
.second .poster{
    display: flex;
    gap: 10px;
}
.second .imgbox{
    width: 290px;
    height: 168px;
}
.second .imgbox img{
    width: 100%;
    height: 100%;
    border-radius: 12px;
    cursor: pointer;
}
.second .imginfo{
    width: 290px;
    margin-top: 24px;
    text-align: center;
}

.sec_h3{
    color: #fff;
    font-size: 18px;
    cursor: pointer;
}
.sec_p{
    font-size: 16px;
    color: #fff;
}


.swiper-button-next::after,
.swiper-button-prev::after{
    color: #fff;
} 