.header{
    width: 100%;
    height: 64px;
    background: #032541;
}
.header .inner{
    width: 1216px;
    height: 64px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header .inner .left{
    display: flex;
    align-items: center;
    gap: 24px;
}
.header .inner .left .gnb{
    display: flex;
    gap: 31px;
    color: #fff;
}
.header .inner .left .gnb li{
    font-size: 16px;
    font-weight: 700;
    cursor: pointer;
}
.header .inner .left h1{
    width: 154px;
}
.header .inner .left h1 img{
    width: 100%;
}
.header .inner .left h1{
    cursor: pointer;
}

.header .inner .right{
    display: flex;
    align-items: center;
    gap: 33px;
    color: #fff;
}
.header .inner .right li{
    font-size: 16px;
    font-weight: 700;
    cursor: pointer;
}
.header .inner .right li.ko{
    font-size: 14px;
    font-weight: 600;
    border: 1px solid #fff;
    padding: 4px;
    border-radius: 4px;
}
.header .inner .right li.ko:hover{
    background: #fff;
    color: #032541;
}