.main2{
    width: 100%;
    height: 430px;
    background: #fff;
}
.main2 .main2_inner{
    width: 1216px;
    height: 100%;
    padding: 40px 0;
    margin: auto;
    position: relative;
}
.main2 .main2_inner h2{
    font-size: 22px;
    font-weight: 800;
    color: #000;
    cursor: pointer;
}
.main2 .main2_inner .genre{
    display: flex;
    margin-bottom: 20px;
}
.main2 .main2_inner .genre ul{
    display: flex;
    align-items: center;
    margin-left: 20px;
    border-radius: 999px;
    gap: 4px;
    border: 1px solid #000;
}
.main2 .main2_inner .genre ul li{
    padding: 2px 12px;
    cursor: pointer;
    font-weight: 700;
}
.main2 .main2_inner .genre ul li.first{
    border: 1px solid #000;
    border-radius: 999px;
    background: #062341;
    color: #4C969A;
}
.main2 .poster{
    display: flex;
    gap: 10px;
}
.main2 .imgbox{
    width: 153px;
    height: 228px;
}
.main2 .imgbox img{
    width: 100%;
    height: 100%;
    border-radius: 12px;
    cursor: pointer;
}
.main2 .imginfo{
    width: 153px;
    /* margin-top: 24px; */
}

.fir_h3{
    color: #000;
    font-size: 16px;
    cursor: pointer;
}
.fir_p{
    font-size: 14px;
    color: #8499AA;
    margin-top: 4px;
}


.swiper-button-next::after,
.swiper-button-prev::after{
    color: #fff;
} 