.banner2{
    width: 100%;
    height: 265px;
    display: flex;
    background: #521A75;
    padding: 30px 0 0 352px;
    color: #fff;
    margin-top: 40px;
}
.banner2 .ba2_left{
    width: 1216px;
    margin-left: -30px;
    margin-right: 20px;
}
.banner2 .ba2_left .ba2_p{
    padding: 20px 0;
}
.banner2 .ba2_left .ba2_btn{
    padding: 8px 12px;
    background: #805BE6;
    color: #fff;
    border-radius: 6px;
}
.banner2 .ba2_right{
    margin-top: 50px;
    line-height: 24px;
}
.banner2 .ba2_right li{
    color: rgba(255, 255, 255, 0.7);
    list-style: square;
    font-size: 14px;
    letter-spacing: -1px;
}