.footer{
    width: 100%;
    background: #032541;
}
.footer_inner{
    width: 1216px;
    margin: auto;
    padding: 41px 0 96px 250px;
    display: flex;
}
.footer_inner .f_left h1{
    width: 131px;
}
.footer_inner .f_left h1 img{
    width: 100%;
}
.footer_inner .f_left button{
    color: #0CA9DC;
    background: #fff;
    font-size: 18px;
    font-weight: 700;
    padding: 12px 16px;
    border-radius: 8px;
    margin-top: 45px;
}

.footer_inner .f_right{
    display: flex;
    margin: 40px 0 0 40px;
    gap: 45px;
}
.footer_inner .f_right ul li{
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    font-weight: 400;
}
.footer_inner .f_right ul li:first-child{
    color: #fff;
    font-size: 18px;
    font-weight: 700; 
}